.logo{display:flex;position:relative;justify-content:flex-start;flex-direction:column;width:30.5%;top:40%}.logo-brain,.logo-text{width:100%}.shrink{-webkit-animation:shrinkBrain 1s ease 0s 1 forwards;animation:shrinkBrain 1s ease 0s 1 forwards}@-webkit-keyframes shrinkBrain{0%{opacity:1;-webkit-transform:scale(1);transform:scale(1)}100%{opacity:0;-webkit-transform:scale(0);transform:scale(0);display:none}}@keyframes shrinkBrain{0%{opacity:1;-webkit-transform:scale(1);transform:scale(1)}100%{opacity:0;-webkit-transform:scale(0);transform:scale(0);display:none}}

