#dashboard{position:relative;text-align:center;width:100%;height:24%}#dashboard .header-logo{width:30.5%;margin:0.8rem 0}#dashboard .scoreboard{width:100%;display:flex}#dashboard .scoreboard-p1,#dashboard .scoreboard-p2{width:50%;background-color:#6c7983;color:#9fa7ae;padding:0.8rem;transition-duration:1s}#dashboard .scoreboard-p1 h2,#dashboard .scoreboard-p2 h2{font-size:1.2rem}#dashboard .scoreboard-p1 .score,#dashboard .scoreboard-p2 .score{font-family:alternate-gothic-no-3-d, sans-serif;font-size:3.25rem;font-weight:100;line-height:3.25rem;transition-duration:1s;letter-spacing:0.05rem}#dashboard .scoreboard-p1 .score .pts,#dashboard .scoreboard-p2 .score .pts{font-size:1.75rem;margin-left:0.5rem;transition-duration:1s}#dashboard .scoreboard-p1{margin-right:0.225rem;text-align:left;padding-left:2rem;-webkit-animation:slideRt 1s ease 0s 1 forwards;animation:slideRt 1s ease 0s 1 forwards}#dashboard .scoreboard-p2{margin-left:0.225rem;text-align:right;padding-right:2rem;-webkit-animation:slideLt 1s ease 0s 1 forwards;animation:slideLt 1s ease 0s 1 forwards}#dashboard .scoreboard .turn{color:#fff}#dashboard .turn-indicators{display:flex;justify-content:space-between;margin-top:0.45rem;width:100%;height:0.5rem}#dashboard .turn-indicators-p1,#dashboard .turn-indicators-p2{width:50%;height:100%}#dashboard .turn-indicators-p1{margin-right:0.225rem;background-color:#FF758E}#dashboard .turn-indicators-p2{margin-left:0.225rem;background-color:#3596E3}#dashboard .turn-indicators .inactive{background-color:#90989e !important}#dashboard .clock{position:absolute;width:14vh;height:14vh;min-width:108px;min-height:108px;border:0.45rem solid #fff;box-shadow:inset 0px 0px 0px 6px #6c7983;border-radius:50%;left:50%;-webkit-transform:translateX(-50%) translateY(-64%);transform:translateX(-50%) translateY(-64%);background-color:#403839;transition-duration:1s;color:#fff;display:flex;flex-direction:column;justify-content:center;align-items:center;font-family:alternate-gothic-no-3-d, sans-serif;font-size:1.9rem}#dashboard .clock .round-text{font-size:0.7rem;font-family:azo-sans-web, sans-serif}#dashboard .hide{display:none}#dashboard .start{z-index:10;background-color:#75B1BA;box-shadow:inset 0px 0px 0px 6px #6c7983}#dashboard .start:hover{cursor:pointer}.shake{-webkit-animation:shaker 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;animation:shaker 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}.clock-shake{-webkit-animation:clockShaker 0.6s cubic-bezier(0.96, 0.07, 0.19, 0.97) 0s 5 both;animation:clockShaker 0.6s cubic-bezier(0.96, 0.07, 0.19, 0.97) 0s 5 both;background-color:#403839 !important;color:#fff !important}@-webkit-keyframes slideRt{0%{-webkit-transform:translateX(-100%);transform:translateX(-100%)}100%{-webkit-transform:translateX(0%);transform:translateX(0%)}}@keyframes slideRt{0%{-webkit-transform:translateX(-100%);transform:translateX(-100%)}100%{-webkit-transform:translateX(0%);transform:translateX(0%)}}@-webkit-keyframes slideLt{0%{-webkit-transform:translateX(100%);transform:translateX(100%)}100%{-webkit-transform:translateX(0%);transform:translateX(0%)}}@keyframes slideLt{0%{-webkit-transform:translateX(100%);transform:translateX(100%)}100%{-webkit-transform:translateX(0%);transform:translateX(0%)}}@-webkit-keyframes shaker{10%,90%{-webkit-transform:translate3d(-1px, 0, 0);transform:translate3d(-1px, 0, 0)}20%,80%{-webkit-transform:translate3d(2px, 0, 0);transform:translate3d(2px, 0, 0)}30%,50%,70%{-webkit-transform:translate3d(-4px, 0, 0);transform:translate3d(-4px, 0, 0)}40%,60%{-webkit-transform:translate3d(4px, 0, 0);transform:translate3d(4px, 0, 0)}}@keyframes shaker{10%,90%{-webkit-transform:translate3d(-1px, 0, 0);transform:translate3d(-1px, 0, 0)}20%,80%{-webkit-transform:translate3d(2px, 0, 0);transform:translate3d(2px, 0, 0)}30%,50%,70%{-webkit-transform:translate3d(-4px, 0, 0);transform:translate3d(-4px, 0, 0)}40%,60%{-webkit-transform:translate3d(4