.scores-table .score-row{display:flex;justify-content:space-between;list-style:none;margin:0.25rem 0;font-size:0.85rem}.scores-table .score-row:nth-child(odd) div{background-color:rgba(53,150,227,0.1)}.scores-table .score-row:nth-child(even) div{background-color:rgba(255,117,142,0.1)}.scores-table .score-row div{padding:0.5rem 0.15rem}.scores-table .score-row div:first-child{width:15%;margin-right:0.25rem;font-weight:bold}.scores-table .score-row div:nth-child(2){width:45%;margin-right:0.25rem}.scores-table .score-row div:nth-child(3){width:20%;margin-right:0.25rem}.scores-table .score-row div:last-child{width:20%;margin-right:0.25rem}.scores-table .score-row .score-header{background-color:transparent !important;font-weight:bold}.noselect{-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}

.input-error{color:#FF758E;font-size:0.9rem;margin-bottom:0.5rem}.input-box{width:100%;font-size:1rem;padding:0.65rem 0.8rem;border-radius:3px;margin-bottom:1rem;box-shadow:0px 2px 8px rgba(0,0,0,0.15);transition-duration:1s}.input-box::-webkit-input-placeholder{color:rgba(144,152,158,0.925)}.input-box::-ms-input-placeholder{color:rgba(144,152,158,0.925)}.input-box::placeholder{color:rgba(144,152,158,0.925)}.input-box:focus{box-shadow:0px 2px 2px rgba(0,0,0,0.25)}.box-1{border:2px solid rgba(255,117,142,0.2);color:#FF758E}.box-1:focus{border-bottom:2px solid rgba(255,117,142,0.5)}.box-2{border:2px solid rgba(53,150,227,0.2);color:#3596E3}.box-2:focus{border-bottom:2px solid rgba(53,150,227,0.5)}.start-btn{padding:0.4rem 0.25rem 0.15rem;margin:1rem 0;font-family:alternate-gothic-no-3-d, sans-serif;font-weight:400;font-size:1.75rem;border-radius:30px;width:90%;background-color:#75B1BA;color:#ffffff;border:3px solid #75B1BA;transition-duration:0.75s}.start-btn:hover{cursor:pointer;background-color:#403839;border:3px solid #403839}

